import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';
import {Observable} from "rxjs";
import {Collections, Content} from "../../app/app.models";
import {AngularFirestore} from "@angular/fire/firestore";

@IonicPage()
@Component({
  selector: 'page-programa-materia',
  templateUrl: 'programa-materia.html',
})
export class ProgramaMateriaPage {

  private id: string = this.navParams.get('id');
  public materia$: Observable<Content>;
  public bloques$: Observable<Content[]>;

  constructor(
    private afs: AngularFirestore,
    private navParams: NavParams,
    private navCtrl: NavController,
  ) {
  }

  ionViewDidLoad() {
    this.materia$ = this.afs.collection(Collections.CONTENT).doc<Content>(this.id).valueChanges();
    this.bloques$ = this.afs.collection<Content>(Collections.CONTENT, ref => ref.where('parent_id', '==', this.id)).valueChanges();
  }

  openBloque(id: string) {
    this.navCtrl.push('ProgramaBloquePage', {id});
  }

}



